.grid-test-wrapper{
  display: grid;
  grid-template-areas: 
  'grid1 grid2'
  'grid3 grid3'
  'grid4 grid5'
  'grid6 grid6';
  justify-items: center;

a{
  color: $darkgray;
  text-decoration: none;
}
}
.grid-icon-wrapper{
display: grid;
grid-template-columns: 1fr 1fr;
justify-items: center;

a{
  color: $darkgray;
  text-decoration: none;
}
}

.grid-solo{
 display: grid;
 justify-items: center;
 grid-column: 1/3;
}

.question-header{
  display: flex;
  flex-direction: column;
  align-items: center;
  h1{
    margin: 1vh 0px;
  }
}

.breakline{
  height: 4px;
  width: 40%;
  background-color: $darkgray;
}
.question-container{
display: flex;
justify-content: center;
flex-direction: column  ;
}

.question{
margin: 2vh 10%;
p{
  text-align: end;
}
 span{
   font-weight: 600;
   text-align: start;
 }
}

.number-text{
  display: flex;
  justify-content: space-between;
}
.question-input{
  display: flex;
  justify-content: flex-end;
  margin-top: 5px;
  p{
    color: $darkgray;
   
  }
  input{
    background-color: $lightgray;
    border: none;
    border-radius: 7px;
    margin-right: 3px;
    text-align: end;
    padding: 5px 5px;
  }
  input:focus{
    border: 2px solid $gray;
  }
}

/* --------> INFO SIDE STYLES <---------- */

#info{
height: 100vh;
width: 100%;
display: flex;
background-image: url(../images/milk-phone.png);
background-size: cover;
}

.arlalogoinfo{
  width: 45%;
  height: auto;
  margin-top: 20px;
}

.underoverskrift{
  color: $darkgray;
  font-weight: 700;
  border-bottom: 3px solid $darkgray;
  width: 160px;
  text-align: center;
}
.infoinner{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

ul{
  list-style-position: outside;
}
li {
  margin-left: 10px;
  display: list-item;
  line-height: 30px;
  margin-bottom: 20px;
}

h4{
  font-size: 30px;
  margin: 30px 0 20px;
}

.infolist{
width: 80%;
}